	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Buggy Browsers Deux","date":"13 Jan 2003","slug":"/2003-01-13-buggy-browsers-deux/","tags":"Y2003"},"html":"<p>Update: Mark has now <a title=\"How to hide CSS from Safari: solution [dive into mark]\" href=\"http://diveintomark.org/archives/2003/01/12.html#how_to_hide_css_from_safari_solution\">found</a> a bug to hide CSS from only Safari. He's not quite correct, of course, since the bug also hides CSS from <a title=\"Konqueror\" href=\"http://www.konqueror.org/\">Konqueror</a>, which also uses KHTML. But then he tested on only <acronym title=\"Windows, Macintosh\">devilish operating systems</acronym>, and not on systems that are <acronym title=\"Linux, BSD\">good and pure</acronym>.</p>"},"next":{"fields":{"title":"Buggy Browsers","date":"10 Jan 2003","slug":"/2003-01-10-buggy-browsers/"}},"previous":{"fields":{"title":"To Book or Not To Book","date":"13 Jan 2003","slug":"/2003-01-13-to-book-or-not-to-book/"}}},{"node":{"fields":{"title":"Buggy Browsers","date":"10 Jan 2003","slug":"/2003-01-10-buggy-browsers/","tags":"Y2003"},"html":"<p>Mark Pilgrim <a title=\"Should Safari be intentionally buggy? [dive into mark]\" href=\"http://diveintomark.org/archives/2003/01/09.html#should_safari_be_intentionally_buggy\">pleads for buggy browsers</a>. Let him eat Netscape 4. Buggy browsers are never good, and we have enough of them already.</p>"},"next":{"fields":{"title":"Women and IT","date":"09 Jan 2003","slug":"/2003-01-09-women-and-it/"}},"previous":{"fields":{"title":"Buggy Browsers Deux","date":"13 Jan 2003","slug":"/2003-01-13-buggy-browsers-deux/"}}},{"node":{"fields":{"title":"Women and IT","date":"09 Jan 2003","slug":"/2003-01-09-women-and-it/","tags":"Y2003"},"html":"<p><a href=\"http://weblog.burningbird.net/fires/000809.htm\">BurningBird</a> has been relating the frustrations of being a female Unix expert. Way back in August 2001 there was a discussion of Women in IT, for which I wrote a <a href=\"http://shanson.editthispage.com/stories/storyReader$703\">response</a> that never got imported into my new blog. Maybe some parts are now relevant, so I'll repeat the some of it  here. </p>\n<hr />\n<p>Scripting News has started a discussion (<a href=\"http://scriptingnews.userland.com/backissues/2001/08/25\">here</a> and <a href=\"http://scriptingnews.userland.com/backissues/2001/08/26\">here</a>) about women in the IT industry, why there are so few of them, and wouldn't it be better if there were more. The responses from women that he's posted seem to agree that he doesn't get it. Maybe it's like the joke about what women really want. If you have to ask, you probably can't provide it anyway. Then again, if you never ask, you'll never find out. </p>\n<p>The situation in Germany is probably worse, since there are so many factors against working women in general, especially those with children. We've complained about these numerous times here: rare and expensive day care, half-day schools, long maternity leaves, etc.</p>\n<p>At my company, of our 8 network administrators, 2 are women, which I think is a unusually high percentage. (One is currently on maternity leave; she plans to come back half-time in January.) Of the 40 or so programmers, currently none are women, although we had two women who left the company last year.</p>\n<p>Interestingly, of those 4 women, only one programmer came to the<br />\nprofession via the <i>Gymnasium</i>->University route. The two admins both took the <i>Realschule</i>->Vocational School track, while the other programmer was a career switcher with training  paid for the by the <i>Arbeitsamt</i> (Unemployment Agency). I would guess that 80% of our programmers and admins come from the University.</p>\n<p>Secondary education in Germany is traditionally split at the 5th grade into university prep (<i>Gymnasium</i>) and vocational school prep (<i>Realschule</i> and <i>Hauptschule</i>). Both our female admins have told stories of how their teachers tried to steer them away from math and technical courses.</p>"},"next":{"fields":{"title":"Samhain - File Integrity and Intrusion Detection","date":"08 Jan 2003","slug":"/2003-01-08-samhain-file-integrity-and-intrusion-detection/"}},"previous":{"fields":{"title":"Buggy Browsers","date":"10 Jan 2003","slug":"/2003-01-10-buggy-browsers/"}}}],"pathPrefix":"","first":false,"last":false,"index":796,"pageCount":1101}}

